  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #c8c8c8; }
  .soft-and-wet-page .container {
    width: 20%;
    height: 0;
    padding-bottom: calc(55% * 9 / 16); }
    .soft-and-wet-page .container .josuke {
      position: relative;
      width: 115%;
      margin-left: -40%; }
    .soft-and-wet-page .container .soft-and-wet {
      position: relative;
      width: 100%;
